import React, { Component } from "react";
import "./index.scss";
import { Icon } from "antd-mobile";
class Notes extends Component {
  constructor(props) {
    super(props);
    this.state = {
      appNote: []
    };
  }
  render() {
    return (
      <div className="mynote">
        <div className="header">
          <Icon type="left" onClick={this.goback.bind(this)} />
          <span className="note">我的笔记</span>
          <span className="add" onClick={this.goEditor.bind(this)}>
            <Icon type="plus" />
          </span>
        </div>
        <ul className="content">
          {this.state.appNote.map((item, index) => {
            return (
              <li key={index} onClick={this.noteHandle.bind(this, item.id)}>
                <span />
                <span>{item.value}</span>
                <span>
                  <Icon type="right" />
                </span>
              </li>
            );
          })}
          {/* {this.state.appNote.forEach(item => {
            return (
              <li>
                <span />
                <span>{item.id}</span>
                <span>
                  <Icon type="right" />
                </span>
              </li>
            );
          })} */}
        </ul>
      </div>
    );
  }
  noteHandle(id) {
    this.props.history.push(`/detail/${id}`);
  }
  componentDidMount() {
    if (JSON.parse(localStorage.getItem("appNote"))) {
      this.setState({
        appNote: JSON.parse(localStorage.getItem("appNote"))
      });
      console.log(JSON.parse(localStorage.getItem("appNote")));
    }
  }
  goback() {
    this.props.history.push("/charpter");
  }
  goEditor() {
    this.props.history.push("/detail");
  }
}
export default Notes;
