import React, { Component } from "react";
import "./home.scss";
import { Link } from "react-router-dom";
import { Button } from 'antd-mobile'
class Home extends Component {
  render() {
    return (
      <div className="homeContainer">
        <ul className="homeReading">
          <Link to="/charpter">
            <li>
              <Button type="primary">首次阅读</Button>
            </li>
          </Link>
          <Link to="/charpter">
            <li className="continue">
              <Button type="primary">继续阅读</Button>
            </li>
          </Link>
        </ul>
        <div />
      </div>
    );
  }
}

export default Home;
//脚手架会把src目录下的路径进行编译 所以不能外部引入关于src目录的文件  但是在src内部的文件就行   因为那时候就是可以找到的
