import React, { Component } from "react";
import "./index.scss";
import { Icon, TextareaItem, Button, Toast } from "antd-mobile";
import "./index.scss";
import axios from "axios";
import BScroll from "better-scroll";
//得存个全局变量 因为固定文章 保存状态 存入localstorage吧
class Ly extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showLy: false,
      lyConent: "",
      ly: []
    };
  }
  /**
   * 解决ios上需要点击两次才能触发点击事件
   *  */
  iScrollClick() {
    if (/iPhone|iPad|iPod|Macintosh/i.test(navigator.userAgent)) return true;
    if (/Chrome/i.test(navigator.userAgent)) return /Android/i.test(navigator.userAgent);
    if (/Silk/i.test(navigator.userAgent)) return false;
    if (/Android/i.test(navigator.userAgent)) {
      var s = navigator.userAgent.substr(navigator.userAgent.indexOf("Android") + 8, 3);
      return parseFloat(s[0] + s[3]) < 44 ? false : true;
    }
  }
  componentDidMount() {
    this.scrollView = this.refs.scrollView;
    this.bScroll = new BScroll(this.scrollView, {
      probeType: 3,
      click: this.iScrollClick()
    });
    axios.get("/ly").then(res => {
      console.log(res);
      this.setState({
        ly: res.data.ly
      });
    });
  }
  render() {
    return (
      <div className="ly">
        {this.state.showLy ? (
          <div className="addLy_box">
            <div className="ly_input">
              <TextareaItem
                placeholder="请输入您的留言..."
                rows={8}
                count={100}
                onChange={this.lyConentChange.bind(this)}
              />
              <div className="action">
                <Button onClick={this.cancelLy.bind(this)} style={{ width: 60, height: 30, marginRight: 10 }}>
                  取消
                </Button>
                <Button
                  onClick={this.confirmHandle.bind(this)}
                  style={{ width: 60, height: 30, marginRight: 10 }}
                  type="primary"
                >
                  确定
                </Button>
              </div>
            </div>
          </div>
        ) : (
          ""
        )}

        <div className="l_header">
          <Icon type="left" className="left" onClick={this.goback.bind(this)} />
          <span className="note">更多评论</span>

          <span className="addLy" onClick={this.showLyHandle.bind(this)}>
            添加
          </span>
        </div>
        <div className="ly_content" ref="scrollView">
          {/* 教程是作用于ref拿到的元素的第一层 但是 设置overflow属性是ref所作用的dom也就是document.querySelector拿到的元素 */}
          <div className="l_wrapper">
            <div className="ly_contentBox">
              {this.state.ly.map((item, index) => {
                return (
                  <div key={index} className="word">
                    <div className="time">
                      <span>{item.lyDate}</span>
                      <span>{item.lyTime}</span>
                    </div>
                    <span className="user">匿名</span>
                    <div>{item.lyInfo}</div>
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    );
  }
  getTime() {
    let time = null;
    var hour = new Date().getHours();
    var minutes = new Date().getMinutes() + 1;
    var second = new Date().getSeconds();
    return (time = "" + hour + ":" + minutes + ":" + second + "");
  }
  getDate() {
    let date = null;
    var year = new Date().getFullYear();
    var month = new Date().getMonth() + 1;
    var day = new Date().getDate();

    return (date = "" + year + "-" + month + "-" + day + "");
  }
  lyConentChange(e) {
    console.log(e);
    this.setState({
      lyConent: e
    });
  }
  confirmHandle() {
    console.log(this.getTime());
    let params = {
      lyInfo: this.state.lyConent,
      lyDate: this.getDate(),
      lyTime: this.getTime()
    };

    axios.post("/add/ly", { params }).then(res => {
      //前端页面展示
      let ly = this.state.ly;
      ly.unshift(params);
      console.log(res);
      Toast.success("添加留言成功");
      this.setState({
        showLy: false,
        ly
      });
    });
  }
  showLyHandle() {
    this.setState({
      showLy: true
    });
  }
  cancelLy() {
    this.setState({
      showLy: false
    });
  }
  goback() {
    this.props.history.go(-1);
  }
}
export default Ly;
