import React, { Component } from "react";
import "./charpter.scss";
import { CSSTransition } from "react-transition-group";
import { Slider, Icon } from "antd-mobile";
import ReactMarkdown from "react-markdown/with-html";
import axios from "axios";
import language from "jian_fan";
import BScroll from "better-scroll";
let ch = [
  {
    title: "第一章 地理风俗",
    id: 0,
    essay: []
  },
  {
    title: "第二章 名人贤士",
    id: 1,
    essay: []
  },
  {
    title: "第三章 民间艺术",
    id: 2,
    essay: []
  },
  {
    title: "第四章 旅游景点",
    id: 3,
    essay: []
  },
  {
    title: "第五章 教育基地",
    id: 4,
    essay: []
  },
  {
    title: "第六章 潮州名片",
    id: 5,
    essay: []
  },
  {
    title: "第七章 后记",
    id: 6,
    essay: []
  }
];
const colorlist = [
  {
    color: "#c1b694",
    bgColor: "#c1b694",
    fontColor: "#000"
  },
  {
    color: "gray",
    bgColor: "gray",
    fontColor: "#ccc"
  },
  {
    color: "#c1ae96",
    bgColor: "#c1ae96",
    fontColor: "#000"
  },
  {
    color: "#c1bd96",
    bgColor: "#c1bd96",
    fontColor: "#000"
  },
  {
    color: "#d1b48e",
    bgColor: "#d1b48e",
    fontColor: "#000"
  },
  {
    color: "#6d6e87",
    bgColor: "#6d6e87",
    fontColor: "#000"
  }
];
//这个模块下的全局变量
let speechInstance = null;
class Charpter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentId: "hidden",
      content: "",
      startX: 0,
      showDirect: false,
      isSwitch: false, //false状态下表示菜单栏关闭
      colorIndex: 0,
      isLang: false, //字体菜单默认关闭状态
      fontStyle: false, //默认为简体
      fontInitclientX: 0, //字体设置的初始化位置
      fontPercent: "0%",
      lightInitclientX: 0, //亮度
      lightLastclientX: 0,
      lightPercent: "40%",
      arrow: false, //false 代表箭头👇
      isFirst: true,
      title: "",
      chapterIndex: 1,
      lastid: -1, //最近的一个章节id,
      showMenu: false, //是否展示菜单
      isSpeak: false,
      isSpeakFirst: false,
      isPlay: false,
      isPlayScreen: false, //人声阅读的时候是否全屏
      showPlay: false, //是否展示播放菜单
      ly: []
    };
  }
  render() {
    return (
      <div className="reading" ref="reading">
        {/* <h2 className="readCharpter">第一章 地理风俗</h2> */}

        <div className="wrapper" ref="scrollView">
          <div className="c_content">
            <p className="title">{this.state.title}</p>
            <div className="readContent" ref="readContent" onClick={this.showMenuHandle.bind(this)}>
              <ReactMarkdown source={this.state.content} escapeHtml={false} />
            </div>
          </div>
        </div>

        <div className="setMenu" ref="setMenu">
          <div className="setColor">
            <ul>
              <li className="setIcon" />
              {colorlist.map((item, index) => {
                return (
                  <li
                    key={index}
                    className={index === this.state.colorIndex ? "current" : ""}
                    onClick={this.changeColor.bind(this, index, item)}
                  >
                    <div className="bg" />
                    <div className="circle" style={{ background: item.color }} />
                  </li>
                );
              })}
            </ul>
          </div>
          <div className="setLight">
            <span className="light" />
            <Slider
              style={{ marginLeft: 10, marginRight: 10, width: 240 }}
              defaultValue={10}
              min={0}
              max={24}
              onChange={this.lightProgressHandle()}
              onAfterChange={this.log("afterChange")}
            />
          </div>
          <span className="progress-num">{this.state.lightPercent}</span>
        </div>
        {/* 设置语言 */}
        <div className="lang" ref="langMenu">
          <div className="setLang">
            <span className="font">简</span>
            <label>
              <input onClick={this.SwitchFontStyle.bind(this)} className="mui-switch mui-switch-anim" type="checkbox" />
            </label>
            <span className="font">繁</span>
          </div>
          <div className="setProgress">
            <span>字体大小</span>
            <Slider
              style={{ marginLeft: 10, marginRight: 10, width: 240 }}
              defaultValue={0}
              min={0}
              max={24}
              onChange={this.fontProgressHandle()}
              onAfterChange={this.log("afterChange")}
            />
            <span>{this.state.fontPercent}</span>
          </div>
        </div>
        <CSSTransition
          in={this.state.showMenu}
          timeout={600}
          classNames="sliderMenu"
          onExited={() => {
            console.log("退出");
          }}
        >
          <div className="set">
            <span className={this.state.arrow ? "switch" : ""} onClick={this.switchHandle.bind(this)} />
            <ul className="setCategory">
              <li onClick={this.showDirect.bind(this)}>
                <img src={require("../../static/images/content@2x.png")} alt="" />
                <span className="text">目录</span>
              </li>
              <li onClick={this.go.bind(this)}>
                <img src={require("../../static/images/note@2x.png")} alt="" />
                <span className="text">笔记</span>
              </li>
              <li onClick={this.openLangMenu.bind(this)}>
                <img src={require("../../static/images/font@2x.png")} alt="" />
                <span className="text">字体</span>
              </li>
              <li onClick={this.opensetHandle.bind(this)}>
                <img src={require("../../static/images/set@2x.png")} alt="" />
                <span className="text">设置</span>
              </li>
            </ul>
            <div className="pen" onClick={this.editHandle.bind(this)}>
              <img src={require("../../static/images/pen.png")} alt="" />
            </div>
          </div>
        </CSSTransition>
        <div
          ref="direct"
          className="directory"
          onTouchStart={this.dTouchStartHandle.bind(this)}
          onTouchMove={this.dTouchMoveHandle.bind(this)}
          onTouchEnd={this.dTouchEndHandle.bind(this)}
        >
          <h2>认知潮州</h2>
          {ch.map((item, index) => {
            return (
              <ul key={index}>
                <p
                  onClick={() => {
                    this.showCharpter(item.id);
                  }}
                >
                  {item.title}
                </p>
                <CSSTransition
                  in={item.id === this.state.currentId}
                  timeout={400}
                  classNames="slider"
                  onExited={() => {
                    console.log("退出");
                  }}
                >
                  <div className="dWrpper">
                    {item.essay
                      ? item.essay.map((v, i) => {
                          return (
                            <li
                              className={this.state.chapterIndex === i ? "active" : ""}
                              onClick={this.changeReadContent.bind(this, v.info, v.title, i)}
                              key={i}
                            >
                              {v.title}
                            </li>
                          );
                        })
                      : " "}
                  </div>
                </CSSTransition>
              </ul>
            );
          })}
        </div>
        <div className="topNav" ref="topNav">
          <span onClick={this.lyHandle.bind(this)}>
            <div className="dot">{this.state.ly.length}</div>
            <i className="iconfont">&#xe748;</i>
          </span>
          <span onClick={this.speakHandle.bind(this)}>
            <i className="iconfont">&#xe606;</i>
          </span>
        </div>
        <div className="setVoice" ref="voice">
          <div className="video">
            <span>
              {" "}
              <i className="iconfont">&#xe62f;</i>
            </span>
            <span>
              {" "}
              {this.state.isPlay ? (
                <i onClick={this.playHandle.bind(this)} className="iconfont">
                  &#xe61d;
                </i>
              ) : (
                <i onClick={this.playHandle.bind(this)} className="iconfont">
                  &#xe662;
                </i>
              )}
            </span>
            <span>
              <i className="iconfont">&#xe62e;</i>
            </span>
            {/* <span>
              <img src={require("../../static/images/play.png")} alt="" />
            </span> */}
          </div>
          <div className="exitBook" onClick={this.exitBook.bind(this)}>
            {" "}
            <span>退出听书</span>
          </div>
        </div>
      </div>
    );
  }
  //留言
  lyHandle() {
    this.props.history.push("/ly");
  }
  //退出听书
  exitBook() {
    this.refs.voice.style.transition = "all 0.4s";
    this.refs.voice.style.transform = "translateY(100px)";
    this.setState({
      isPlayScreen: false,
      showPlay: !this.state.showPlay,
      isSpeak: false
    });
    speechSynthesis.cancel();
  }
  playHandle() {
    if (this.state.isPlay) {
      speechSynthesis.pause();
    } else {
      speechSynthesis.resume();
    }
    this.setState({
      isPlay: !this.state.isPlay
    });
  }
  voiceHandle() {
    let content = `${this.state.content}`;
    let c = `${content}`.replace(/\n/g, "");
    speechInstance = new SpeechSynthesisUtterance(c.replace(/\!\[\]\([^(]*\)/g, ""));
    speechSynthesis.cancel();

    speechInstance.volume = 0;

    speechInstance.pitch = 4;
    speechSynthesis.speak(speechInstance);
  }

  componentWillUnmount() {
    speechSynthesis.cancel();
  }

  //双击退出全屏
  speakHandle() {
    //展示全屏
    if (!this.state.isPlayScreen) {
      this.setState({
        showMenu: !this.state.showMenu,
        isPlayScreen: !this.state.isPlayScreen
        // isLang: false,
        // isSwitch: false
      });

      this.setState({
        isLang: false,
        isSwitch: false,
        arrow: false
      });
      this.refs.topNav.style.transform = "translateY(-100px)";
      this.refs.langMenu.style.transform = "translateY(300px)";
      this.refs.setMenu.style.transform = "translateY(300px)";

      // this.refs.langMenu.style.opacity = "0";
      // this.refs.setMenu.style.opacity = "0";

      this.refs.topNav.style.transition = "all 0.4s";
    }
    let content = `${this.state.content}`;
    let c = `${content}`.replace(/\n/g, "");
    speechInstance = new SpeechSynthesisUtterance(c.replace(/\!\[\]\([^(]*\)/g, ""));
    let voices = window.speechSynthesis.getVoices();
    console.log(c.replace(/\!\[\]\([^(]*\)/g, ""));
    console.log(speechInstance);
    let vm = this;
    console.log(speechInstance.rate);
    speechInstance.onend = function() {
      console.log("读完了");
      vm.setState({
        isSpeakFirst: false,
        isSpeak: false
      });
      speechSynthesis.cancel();
    };
    speechInstance.onboundary = function(e) {
      // console.log("读完这个单词了");
      //console.log(e);
    };
    if (!this.state.isSpeakFirst) {
      speechSynthesis.speak(speechInstance);
      this.setState({
        isSpeakFirst: true
      });
    }
    if (!this.state.isSpeak) {
      console.log("继续播放");
      speechInstance.rate = 14.0;
      speechSynthesis.resume();
    } else {
      console.log("暂停播放");
      speechSynthesis.pause();
    }
    this.setState({
      isSpeak: !this.state.isSpeak,
      isPlay: true
    });
  }

  editHandle() {
    this.props.history.push("/detail");
  }
  /**
   * 解决ios上需要点击两次才能触发点击事件
   *  */
  iScrollClick() {
    if (/iPhone|iPad|iPod|Macintosh/i.test(navigator.userAgent)) return true;
    if (/Chrome/i.test(navigator.userAgent)) return /Android/i.test(navigator.userAgent);
    if (/Silk/i.test(navigator.userAgent)) return false;
    if (/Android/i.test(navigator.userAgent)) {
      var s = navigator.userAgent.substr(navigator.userAgent.indexOf("Android") + 8, 3);
      return parseFloat(s[0] + s[3]) < 44 ? false : true;
    }
  }

  componentDidMount() {
    axios.get("/ly").then(res => {
      console.log(res);
      this.setState({
        ly: res.data.ly
      });
    });
    // setInterval(() => {
    //   speechSynthesis.getVoices();
    // }, 2000);
    //刷新窗口
    window.addEventListener("beforeunload", () => {
      speechSynthesis.cancel();
    });
    //箭头函数的好处就是this指向
    this.scrollView = this.refs.scrollView;
    this.bScroll = new BScroll(this.scrollView, {
      probeType: 3,
      click: this.iScrollClick(),
      taps: this.props.tap
    });
    ch = [
      {
        title: "第一章 地理风俗",
        id: 0,
        essay: []
      },
      {
        title: "第二章 名人贤士",
        id: 1,
        essay: []
      },
      {
        title: "第三章 民间艺术",
        id: 2,
        essay: []
      },
      {
        title: "第四章 旅游景点",
        id: 3,
        essay: []
      },
      {
        title: "第五章 教育基地",
        id: 4,
        essay: []
      },
      {
        title: "第六章 潮州名片",
        id: 5,
        essay: []
      },
      {
        title: "第七章 后记",
        id: 6,
        essay: []
      }
    ];
    axios.get("/article", { category: 1 }).then(res => {
      let essay = res.data.essay;
      for (let i = 0; i < essay.length; i++) {
        let charpter = {
          info: essay[i].essay.markdown,
          title: essay[i].essay.title
        };
        switch (essay[i].category) {
          case 1:
            ch[0].essay.push(charpter);
            break;
          case 2:
            ch[1].essay.push(charpter);
            break;
          case 3:
            ch[2].essay.push(charpter);
            break;
          case 4:
            ch[3].essay.push(charpter);
            break;
          case 5:
            ch[4].essay.push(charpter);
            break;
          case 6:
            ch[5].essay.push(charpter);
            break;
          case 7:
            ch[6].essay.push(charpter);
            break;
          default:
            break;
        }
      }
      this.setState({
        content: essay[0].essay.markdown,
        title: essay[0].essay.title
      });
    });
  }

  changeReadContent(info, title, index) {
    this.setState({
      content: info,
      title,
      chapterIndex: index
    });
  }
  go() {
    this.props.history.push("/note");
  }
  //改变亮度
  lightProgressHandle() {
    return value => {
      console.log(value);
      let lightPercent = parseInt((value / 24) * 100) + "%";
      this.setState({
        lightPercent
      });
    };
  }
  //改变字体大小
  fontProgressHandle() {
    return value => {
      console.log(value);
      let fontPercent = parseInt((value / 24) * 100) + "%";
      this.setState({
        fontPercent
      });
      let fontSize;

      fontSize = parseInt(14 + (value / 24) * 10);

      console.log(fontSize + "px");
      //最小的字体是12px 最大24px
      this.refs.readContent.style.fontSize = `${fontSize}px`;
    };
  }
  log = name => {
    return value => {
      // console.log(`${name}: ${value}`);
    };
  };
  //亮度
  lightStartHandle(e) {
    this.setState({
      lightInitclientX: e.touches[0].clientX
    });
  }
  lightMoveHandle(e) {
    let moveX = e.touches[0].clientX - this.state.lightInitclientX;

    //translateX相对的
    if (moveX < 0) {
      moveX = 0;
    }
    if (moveX > 208) {
      moveX = 208;
    }
    let percent = moveX / 260 + 0.2;
    this.setState({
      lightPercent: parseInt(percent * 100 + 0.2) + "%"
    });
    this.refs.lightProgress.style.width = `${percent * 260}px`;
    this.refs.Lbutton.style.transform = `translateX(${moveX}px)`;
  }
  lightEndHandle(e) {}
  //字体大小
  fontStartHandle(e) {
    console.log(e.touches[0].clientX);
    this.setState({
      fontInitclientX: e.touches[0].clientX
    });
  }

  fontMoveHandle(e) {
    let moveX = e.touches[0].clientX - this.state.fontInitclientX;
    //translateX相对的
    if (moveX < 0) {
      moveX = 0;
    }
    if (moveX > 160) {
      moveX = 160;
    }
    let percent = moveX / 200 + 0.2;
    this.setState({
      fontPercent: parseInt(percent * 100 + 0.2) + "%"
    });
    this.refs.fontProgress.style.width = `${percent * 200}px`;
    this.refs.Fbutton.style.transform = `translateX(${moveX}px)`;
  }

  fontEndHandle(e) {
    let percent = parseInt(this.state.fontPercent) * 0.01;
    this.refs.readContent.style.fontSize = `${12 + 6 * percent}px`;
    this.setState({
      fontInitclientX: 0
    });
  }
  SwitchFontStyle() {
    if (!this.state.fontStyle) {
      this.setState({
        content: language.traditional(this.state.content)
      });
    } else {
      this.setState({
        content: language.simplified(this.state.content)
      });
    }

    this.setState({
      fontStyle: !this.state.fontStyle
    });

    //切换字体
    if (this.state.fontStyle) {
      this.refs.reading.style.fontFamily = "Microsoft YaHei";
    } else {
      this.refs.reading.style.fontFamily = "SimSun";
    }
  }
  switchHandle() {
    //如果两个菜单都没打开 点击它没有效果
    if (!this.state.isLang && !this.state.isSwitch) {
      return false;
    }
    //如果arrow为false时候return
    // if (this.state.arrow) {
    //   return false;
    // }
    //关
    this.refs.setMenu.style.transition = "all 0.4s";
    if (this.state.isSwitch && !this.state.isLang) {
      this.refs.setMenu.style.transform = "translateY(300px)";
      this.setState({
        isSwitch: !this.state.isSwitch
      });
    }
    if (this.state.isLang && !this.state.isSwitch) {
      this.refs.langMenu.style.transform = "translateY(300px)";
      this.setState({
        isLang: !this.state.isLang
      });
    }
    this.setState({
      arrow: false
    });
  }
  //菜单开关
  opensetHandle() {
    // if (!this.state.isLang) {
    //   return false;
    // }
    if (this.state.isLang) {
      console.log(this.state.isLang);
      this.refs.langMenu.style.transform = "translateY(800px)";
      this.setState({
        isLang: !this.state.isLang
      });
    }
    this.refs.setMenu.style.transition = "all 0.4s";
    //打开
    if (!this.state.isSwitch) {
      this.refs.setMenu.style.transform = "translateY(0)";
    }
    //关闭
    else {
      this.refs.setMenu.style.transform = "translateY(800px)";
    }
    if (this.state.isLang) {
      this.setState({
        isSwitch: !this.state.isSwitch,
        arrow: true
      });
    } else {
      this.setState({
        isSwitch: !this.state.isSwitch,
        arrow: !this.state.arrow
      });
    }
  }
  //字体菜单
  openLangMenu() {
    // if (!this.state.isSwitch) {
    //   return false;
    // }
    if (this.state.isSwitch) {
      this.refs.setMenu.style.transform = "translateY(800px)";
      this.setState({
        isSwitch: !this.state.isSwitch
      });
    }
    this.refs.langMenu.style.transition = "all 0.4s";
    if (!this.state.isLang) {
      this.refs.langMenu.style.transform = "translateY(0)";
    }
    //关闭
    else {
      this.refs.langMenu.style.transform = "translateY(800px)";
    }
    if (this.state.isSwitch) {
      this.setState({
        isLang: !this.state.isLang,
        arrow: true
      });
    } else {
      this.setState({
        isLang: !this.state.isLang,
        arrow: !this.state.arrow
      });
    }
  }

  //切换颜色
  changeColor(index, color) {
    console.log(color);
    this.setState({
      colorIndex: index
    });
    //设置背景色
    document.body.style.background = `${color.bgColor}`;
    this.refs.reading.style.color = `${color.fontColor}`;
  }
  //展示菜单
  showMenuHandle() {
    //如果是在播放时候 点击别的地方

    if (this.state.isPlayScreen) {
      console.log("走這步");
      this.refs.voice.style.transition = "all 0.4s";

      if (!this.state.showPlay) {
        this.refs.voice.style.transition = "all 0.4s";
        this.refs.voice.style.transform = "translateY(0)";
      } else {
        this.refs.voice.style.transition = "all 0.4s";
        this.refs.voice.style.transform = "translateY(100px)";
      }
      this.setState({
        showPlay: !this.state.showPlay
      });
    } else {
      console.log("走下一步");
      //点击关闭时候
      if (this.state.showMenu) {
        this.refs.langMenu.style.transform = "translateY(300px)";
        this.refs.setMenu.style.transform = "translateY(300px)";

        // this.refs.langMenu.style.opacity = "0";
        // this.refs.setMenu.style.opacity = "0";

        this.setState({
          isLang: false,
          isSwitch: false,
          arrow: false
        });
      } else {
        // this.refs.langMenu.style.opacity = "1";
        // this.refs.setMenu.style.opacity = "1";
      }
      setTimeout(() => {
        if (this.state.showMenu) {
          //顶部导航消失动画
          this.refs.topNav.style.transform = "translateY(-100px)";
          this.refs.topNav.style.transition = "all 0.4s";
        } else {
          this.refs.topNav.style.transition = "all 0.4s";
          this.refs.topNav.style.transform = "translateY(0)";
        }
        this.setState({
          showMenu: !this.state.showMenu
        });
      }, 20);
    }
  }

  showCharpter(id) {
    if (this.state.lastid !== id) {
      this.setState({
        chapterIndex: -1,
        lastid: id
      });
    }
    //用数据改变而不是通过操作dom ---通常操作数据可能跟css配合 效果刚好
    if (this.state.currentId === id) {
      this.setState(() => ({ currentId: "hidden" }));
      return;
    }
    this.setState(() => ({
      currentId: id
    }));
  }
  dTouchStartHandle(e) {
    let TouchstartX = e.touches[0].clientX;
    this.setState(() => ({
      startX: TouchstartX
    }));
  }
  dTouchMoveHandle(e) {
    //移动多少 就跟着移动多少
    let moveX = e.touches[0].clientX - this.state.startX;
    if (moveX > 0) {
      moveX = 0;
    }
    this.refs.direct.style.transition = "all 0.4s";
    this.refs.direct.style.transform = `translateX(${moveX}px)`;
  }
  dTouchEndHandle(e) {
    let endX = e.changedTouches[0].clientX - this.state.startX;
    console.log(endX);
    if (endX > -50) {
      this.refs.direct.style.transition = "all 0.4s";
      this.refs.direct.style.transform = `translateX(0)`;
      return;
    }
    this.refs.direct.style.transition = "all 0.4s";
    this.refs.direct.style.transform = `translateX(-500px)`;
    this.setState(() => ({
      showDirect: false
    }));
  }
  showDirect() {
    if (!this.state.showDirect) {
      this.refs.direct.style.transition = "all 0.4s";
      this.refs.direct.style.transform = `translateX(0)`;
    } else {
      this.refs.direct.style.transition = "all 0.4s";
      this.refs.direct.style.transform = `translateX(-500px)`;
    }

    this.setState(() => ({
      showDirect: !this.state.showDirect
    }));
  }
}

export default Charpter;

