import React, { Component } from "react";
import "./static/css/index.scss";
import "./App.scss";
import Home from "./page/Home";
import Charpter from "./page/Charpter";
import { HashRouter, Route } from "react-router-dom";
import Note from "./page/Note/component/index.js";
import Detail from "./page/Note/component/detail";
import Ly from "./page/Ly/index.js";
import "./app.css";
class App extends Component {
  render() {
    return (
      <HashRouter>
        <div className="App">
          <Route exact path="/" component={Home} />
          <Route path="/charpter" component={Charpter} />
          <Route path="/note" component={Note} />
          <Route path="/detail/:id?" component={Detail} />
          <Route path="/ly" component={Ly} />
        </div>
      </HashRouter>
    );
  }
}

export default App;
